<template>
    <div class="container">
        <div class="main__cars useritem" v-if="users">
            <div class="main__cars-item useritem-box" v-for="user in users" :key="user.id">
                <p>Street: {{ user.address.street }}</p>
                <p>City: {{ user.address.city }}</p>
                <p>Suite: {{ user.address.suite }}</p>
                <p>Zipcode: {{ user.address.zipcode }}</p>
            </div>
        </div>
        <div v-else class="Loading">
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getUsers()
        },
        computed:{
            users() {
                return this.$store.state.user
            }
        },
        methods: {
            getUsers() {
                this.$store.dispatch('getUsers')
            }
        },
    }
</script>
